.navBurger {
  position: fixed;
  z-index: 10;
  left: 40px;
  top: 40px;
  cursor: pointer;
  background: #ffffff;
  box-sizing: border-box;
  background-clip: content-box;
  width: 30px;
  height: 26px;
  border-top: 11px solid transparent;
  border-bottom: 11px solid transparent;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out; }
  .navBurger:before, .navBurger:after {
    content: "";
    position: absolute;
    background: #ffffff;
    height: 4px;
    width: 100%;
    right: 0;
    will-change: rotate;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); }
  .navBurger:before {
    top: -10px; }
  .navBurger:after {
    bottom: -10px; }
  .navBurger.active {
    background: transparent;
    background-clip: content-box; }
  .navBurger.active:before {
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
    top: 0;
    background: #eee;
    width: 110%; }
  .navBurger.active:after {
    bottom: 0;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
    background: #eee;
    width: 110%; }

.main-nav-overlay {
  position: fixed;
  background: rgba(42, 49, 64, 0.5);
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease-in-out;
  overflow: hidden; }
  .main-nav-overlay.open {
    opacity: 0.99;
    visibility: visible; }
    .main-nav-overlay.open li {
      display: block;
      animation: fadeInRight 0.4s cubic-bezier(0.42, 0.83, 0.7, 1.5) forwards;
      animation-delay: 0.4s;
      transform: rotate(7deg);
      transform-origin: right bottom; }
      .main-nav-overlay.open li:nth-of-type(2) {
        animation-delay: 0.5s; }
      .main-nav-overlay.open li:nth-of-type(3) {
        animation-delay: 0.6s; }
      .main-nav-overlay.open li:nth-of-type(4) {
        animation-delay: 0.7s; }
      .main-nav-overlay.open li:nth-of-type(5) {
